.basicLightbox {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    opacity: 0.01;
    transition: opacity 0.4s ease;
    z-index: 1000;
    will-change: opacity;
}

.basicLightbox--visible {
    opacity: 1;
}

.basicLightbox__placeholder {
    max-width: 100%;
    transform: scale(0.9);
    transition: transform 0.4s ease;
    z-index: 1;
    will-change: transform;
}

.basicLightbox__placeholder>iframe:first-child:last-child,
.basicLightbox__placeholder>img:first-child:last-child,
.basicLightbox__placeholder>video:first-child:last-child {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    max-width: 95%;
    max-height: 95%;
}

.basicLightbox__placeholder>iframe:first-child:last-child,
.basicLightbox__placeholder>video:first-child:last-child {
    pointer-events: auto;
}

.basicLightbox__placeholder>img:first-child:last-child,
.basicLightbox__placeholder>video:first-child:last-child {
    width: auto;
    height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder,
.basicLightbox--img .basicLightbox__placeholder,
.basicLightbox--video .basicLightbox__placeholder {
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
    transform: scale(1);
}

.film-card__wrapper {
    display: flex;
    width: 806px;
    height: 460px;
    background-color: #fff;
    border-radius: 3px;
    margin: 0 auto;
    padding: 40px 12px;
    position: relative;
}

.description-set {
    gap: 35px;
    display: flex;
}

.description-button {
    text-align: center;
    color: #000;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #000;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.3;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
    padding: 10px 20px;
}

.description-button:hover {
    color: #fff;
    background-color: #ff6b08;
    border-color: rgba(0, 0, 0, 0);
    cursor: pointer;
}

.film-card__image {
    width: 375px;
    height: 478px;
}

.film-card__description-title {
    display: flex;
    justify-content: center;
}

.film-card__description-right {
    margin-left: 20px;
}

.film-card__description-set {
    list-style: none;
    padding: 0;
    margin: 0;
}